/** @jsx jsx */
import { jsx } from '@emotion/core';
import {
  Grid,
  Button,
  Avatar,
  Drawer,
  makeStyles,
  List,
  ListItem,
  Divider,
  ListItemText,
  ListItemIcon,
  useTheme,
  Typography,
} from '@material-ui/core';
import { useAuth } from 'hooks/auth/useAuth';
import DefaultProfile from 'src/images/defaultProfile.png';
import { Link } from 'gatsby';
import { useState, useMemo } from 'react';
import Emoji from 'components/Utils/Emoji';
import { UserMenus } from 'src/constants';
import SetUserInfoButton from 'components/Buttons/SetUserInfoButton';

const UserMenu = () => {
  const { isAuthorized, user } = useAuth();
  const [isOpen, setOpen] = useState(false);
  const theme = useTheme();
  const style = makeStyles({
    button: {
      padding: 0,
      minWidth: 0,
    },
    menuList: {
      width: 260,
      [theme.breakpoints.up('md')]: {
        width: 350,
      },
    },
    userInfo: {
      width: '100%',
      padding: '1rem',
    },
    avatar: {
      [theme.breakpoints.up('md')]: {
        width: '5rem',
        height: '5rem',
      },
    },
    userName: {
      fontWeight: 'bold',
    },
  })();

  const userName = useMemo(() => {
    if (!user) {
      return '';
    }

    if (!user.name) {
      return user.email.split('@')[0];
    } else {
      return user.name;
    }
  }, [user]);

  const needAddingUserInfo = useMemo(() => {
    if (!user) {
      return false;
    }

    return !user.name || user.phoneNumber;
  }, [user]);

  if (!isAuthorized || !user) {
    return null;
  }

  return (
    <div>
      <Button onClick={() => setOpen(!isOpen)} className={style.button}>
        <Grid container alignItems="center">
          <Grid item xs="auto">
            <Avatar src={user.profileImage ?? DefaultProfile} alt={userName} />
          </Grid>
        </Grid>
      </Button>
      <Drawer anchor="right" open={isOpen} onClose={() => setOpen(false)}>
        <div className={style.menuList}>
          <div className={style.userInfo}>
            <Grid container spacing={2} alignItems="center">
              <Grid
                item
                component={Avatar}
                src={user.profileImage ?? DefaultProfile}
                alt={userName}
                className={style.avatar}
              />
              <Grid item component={Typography} className={style.userName}>
                {userName}
              </Grid>
            </Grid>
            {needAddingUserInfo && <SetUserInfoButton />}
          </div>
          <Divider />
          <List>
            {UserMenus.map((menu, index) => (
              <ListItem button component={Link} to={menu.link} key={index}>
                <ListItemIcon>
                  <Emoji emoji={menu.emoji ?? ''} name={menu.name} />
                </ListItemIcon>
                <ListItemText primary={menu.name} />
              </ListItem>
            ))}
          </List>
          <Divider />
          <List>
            <ListItem button component={Link} to="/app/logout">
              <ListItemIcon>
                <Emoji emoji="🖐" name="logout" />
              </ListItemIcon>
              <ListItemText primary="로그아웃" />
            </ListItem>
          </List>
        </div>
      </Drawer>
    </div>
  );
};

export default UserMenu;
