import formatISO from 'date-fns/formatISO';
import addHours from 'date-fns/addHours';
import isBefore from 'date-fns/isBefore';

function getExpiry(expiryHour?: number) {
  const now = new Date();
  if (expiryHour != null) {
    return formatISO(addHours(now, expiryHour));
  } else {
    return '';
  }
}

/**
 * @param data
 * @param expiryHour 단위: Hour
 */
export function setLocalStorageItem(key: string, data: any, expiryHour?: number) {
  if (!window.localStorage) {
    console.warn('There is no localStorage!');
    return;
  }

  const payload = {
    data,
    expiry: getExpiry(expiryHour),
  };

  window.localStorage.setItem(key, JSON.stringify(payload));
}

export function getLocalStorageItem<T>(key: string): T | null {
  if (!window.localStorage) {
    console.warn('There is no localStorage!');
    return;
  }

  const payload = window.localStorage.getItem(key);
  if (payload == null) {
    return null;
  }

  const parsedPayload: { data: T; expiry: string } = JSON.parse(payload);
  const now = new Date();
  const expiry = new Date(parsedPayload.expiry);
  if (isNaN(expiry.getTime()) || isBefore(now, expiry)) {
    return parsedPayload.data;
  } else {
    window.localStorage.removeItem(key);
    return null;
  }
}
