import firebase from 'gatsby-plugin-firebase';
import { useCallback, useEffect, useContext } from 'react';
import { User } from 'models/auth';
import { useAuthState } from 'react-firebase-hooks/auth';
import { isBrowser } from 'src/constants';
import { GlobalContext } from 'src/stores';

function convertFirebaseUserToLubyconUser(user: firebase.User) {
  if (user == null) {
    return null;
  }

  const { providerData } = user;
  const { uid, email, phoneNumber, displayName, photoURL } = providerData[0];
  return {
    id: uid,
    name: displayName,
    profileImage: photoURL,
    email,
    phoneNumber,
  };
}

interface AuthScheme {
  isAuthorized: boolean;
  loading: boolean;
  user: User | null;
  rawUser: firebase.User;
  loginByEmail: (email: string, password: string) => Promise<firebase.auth.UserCredential>;
  logout: () => Promise<void>;
  updateProfile: (data: { name: string }) => Promise<void>;
}
export function useAuth(): AuthScheme {
  const auth: any = isBrowser ? firebase.auth?.() : {};
  const [firebaseUser, loading] = useAuthState(auth);
  const { user, setUser } = useContext(GlobalContext);

  const loginByEmail = useCallback(
    async (email: string, password: string) => {
      await firebase.auth?.().signInWithEmailAndPassword(email, password);
      setUser(convertFirebaseUserToLubyconUser(firebaseUser));
    },
    [firebaseUser, setUser]
  );

  const logout = useCallback(async () => {
    await firebase.auth?.().signOut();
    setUser(convertFirebaseUserToLubyconUser(null));
  }, [setUser]);

  const updateProfile = useCallback(
    async ({ name }: Partial<Pick<User, 'name'>>) => {
      if (firebaseUser && name) {
        await firebaseUser.updateProfile({
          displayName: name,
        });
        setUser(convertFirebaseUserToLubyconUser(firebaseUser));
      }
    },
    [firebaseUser, setUser]
  );

  useEffect(() => {
    if (firebaseUser != null) {
      setUser(convertFirebaseUserToLubyconUser(firebaseUser));
    }
  }, [firebaseUser, setUser]);

  return {
    isAuthorized: Boolean(user),
    loading,
    user,
    rawUser: firebaseUser,
    loginByEmail,
    logout,
    updateProfile,
  };
}
